<template>
  <div >
      <div id="classifys">
      <div id="vm-header">
          <div class="v-ser"><i>&#xe612;</i><span>搜索附近的商家、商品</span></div>
      </div>
      <div id="vm-main">
        <div id="vm-left">
            <ul>
                <li class="vm-left-li" v-for="(item,index) in classify" :key="item.guid" @click="showGoods(item.text,index)"><router-link class="className" to="" >{{item.text}}</router-link></li>
            </ul>
        </div>
        <div id="vm-right">
            <ul>
                <li  v-for="cde in cdetail" :key="cde.guid">
                    <p class="vm-right-tit">{{cde.text}}</p>
                    <div class="vm-right-text">
                        <div class="vm-good" v-for="child in cde.children" :key="child.guid">
                            <img :src="child.imgs" alt="">
                            <p>{{child.text}}</p>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
      </div>
      </div>
  </div>
</template>
<script>
// import vfoot from './vfoot.vue'
    export default {
        data() {
            return {
                classify:[],
                cdetail:[]
            };
        },
        created() {
            this.jQuery.get(this.apiURL + "/classify", result => {
                 if (result.errcode == 0){
                     this.classify = result.data;
                 }
                //  console.log(this.classify)
            });
            this.jQuery.get(this.apiURL + "/cdetail", result => {
                 if (result.errcode == 0){
                     this.cdetail = result.data;
                 }
                 console.log(this.cdetail)
            });
        },
          methods: {
            showGoods(text,index) {
                this.jQuery.get(this.apiURL + "/cdetail", { ctgtext: text }, result => {
                    if (result.errcode == 0) {
                    this.cdetail = result.data;
                    }
                    
                });
                this.jQuery('.vm-left-li').eq(index).addClass('border').siblings().removeClass('border')
            },
          }
    }
</script>
<style>
*{
  margin-bottom:0px;;
  font-style: normal;
  list-style: none;
  margin: 0;
  padding: 0;
}
#classifys{
    background: #fff;
}
#vm-header{
    width:100%;
    height:40px;
    overflow: hidden;
}
.v-ser{
    width:96%;
    height:30px;
    border:1px solid #ddd;
    margin: 8px auto;
    border-radius: 3px;
    line-height: 26px;
    padding-left:10px;
    box-sizing: border-box;
}
.v-ser i{
    font-family: iconfont;
    margin-right: 10px;
    color:#CBCBCB;
}
.v-ser span{
    font-size: 14px;
    color:#757575;
}
#vm-left{
    width:24%;
    margin-top: 5px;
    height:100%;
    background: #F8F8F8;
    overflow: hidden;
    display: flex;
}
#vm-left ul{
    width:100%;
    
}
.vm-left-li{
    height: 43px;
    line-height: 44px;
    border-bottom: 1px solid #e8e8e8;
    border-right: 1px solid #e8e8e8;
    padding-left: 16px;
    box-sizing: border-box;
}
.className{
    font-size: 14px;
    color: #666666;
    white-space: nowrap;
    text-decoration: none;
}
.className:hover{
    text-decoration: none;
    color:#75C67B;
    
}
.border {
    border-left: 3px solid #75C67B;
    background: #fff;
    border-right: 1px solid #fff;
}
vfoot{
    color:red;
}
#vm-main > div {
  float: left;
}
#vm-right{
    width:76%;
    margin-top: 10px;
    height:100%;
    display: flex;
    background: #fff;
    font-size: 14px;
}
#vm-right li{
    margin-top: 10px;
}
.vm-right-tit{
    width:100%;
    height:19px;
    line-height: 19px;
    padding: 5px 0;
    /* color:red; */
    clear: both;
    padding-left: 20px;
    box-sizing: border-box;
    margin: 10px 0;;
}
.vm-good{
    float: left;
    width:30%;
    text-align: center;
    margin-left: 5px;
}
.vm-good img{
    width:90%;
}
.vm-right-text{
    width:100%;
    padding-left: 10px;
    box-sizing: border-box;
}


</style>




