<template>
  <div>
      <div id="order">
          <ul>
              <li class="order-li" v-for="onecar in maincar" :key="onecar.guid">
                  <p class="ord-tit">{{onecar.text}}&nbsp;> <span>已完成</span></p>
                  <p class="ord-time">{{onecar.date}}</p>
                  <div class="order-gd">
                      <div class="gd-only" v-for="child in onecar.childrens" :key="child.guid">
                          <img :src="child.imgs" alt="">
                      </div>
                      <div class="ord-righ">
                          <p class="money">&yen;{{onecar.Tprice}}</p>
                          <p>共{{onecar.childrens.length}}件</p>
                          <span class="da">达达专送</span>
                      </div>
                  </div>
              </li>
          </ul>
          <div class="order-all">
              已显示全部
          </div>
      </div>
  </div>
</template>
<script>
    export default {
        data(){
            return {
                maincar:[]
            }
        },
        created(){
             this.jQuery.get(this.apiURL + "/maincar", result => {
                 if (result.errcode == 0){
                     this.maincar = result.data;
                 }
                //  console.log(this.classify)
            });
        }
    }
</script>
<style>
body{
    background: #F4F4F4;
}
*{
    margin: 0;
    padding:0;
    box-sizing: border-box;
    word-wrap: break-word;
    font-size: 14px;
    font-style: normal;
}
p{
    margin-bottom: 0;
}
.order-li{
    width:100%;
    /* position: relative; */
    color: #333;
    font-size: 0.24rem;
    background-color: #fff;
    opacity: 1;
    max-height: 100%;
    padding: 10px 10px 15px;
    margin-bottom: 10px;
}
.ord-tit{
    width:100%;
    height: 34px;
    font-size: 15px;
    white-space: nowrap;
    color: #333;
    font-weight: bold;
}
.ord-tit span{
    float: right;
    font-weight: 400;
    font-size: 13px;
}
.ord-time{
    margin-top: -14px;
    font-size: 13px;
    color:#B5B5B5;
}
.order-gd{
    width:100%;
    /* display: flex; */
    margin-top: 10px;
    overflow: hidden;

}
.gd-only{
    float:left;
    width:16%;
    margin-left: 10px;
    display: flex;
    flex-direction:column;
    align-items: center;
}
.gd-only img{
    width:100%;
}
.da{
    display: inline-block;
    line-height: 1;
    padding: 2px;
    font-size: 10px;
    color: #16a9ff;
    border: 1px solid #16A9ff;
    border-radius: 2px;
}
.ord-righ{
    float:right;
    text-align: right;
}
.order-all{
    width:100%;
    height:40px;
    background: #fff;
    text-align: center;
    line-height: 40px;
}
.money{
    color:red;
}
    
</style>




