<template>
  <div>
      <div id="maincar">
          <div class="address">
            <div class="addr">海底捞火锅(太平南路店)</div>
          </div>
          <div class="car-index" v-for="onecar in maincar" :key="onecar.guid">
              <div class="car-tit">{{onecar.text}}</div>
              <div class="car-gds" >
                  <div class="good-only" v-for="child in onecar.childrens" :key="child.guid">
                      <img :src="child.imgs" alt="">
                      <p>&yen;{{child.price}}</p>
                  </div>
              </div>
          </div>
      </div>
  </div>
</template>
<script>
    export default {
        data(){
            return {
                maincar:[]
            }
        },
        created(){
             this.jQuery.get(this.apiURL + "/maincar", result => {
                 if (result.errcode == 0){
                     this.maincar = result.data;
                 }
                //  console.log(this.classify)
            });
        }
    }

</script>
<style>
body{
    background: #F4F4F4;
}
*{
    margin: 0;
    padding:0;
    box-sizing: border-box;
    word-wrap: break-word;
    font-size: 14px;
}
#maincar{
    padding: 0 0 99px 0;
    text-align: center;
    background-color: #f4f4f4;
}
.address{
    height:34px;
    display: inline-block;
    vertical-align: top;
    padding: 0 7px;
    border-radius: 32px;
    background-color: #d1e7d3;
    margin: 15px 0 10px 0;
    box-sizing: border-box
}
.addr{
    /* width:100%; */
    margin: 6px auto;
    vertical-align: top;
    line-height: 22px;
    padding: 0 14px 0 31px;
    border-radius: 22px;
    color: #fff;
    background: #47b34f url(/static/img/loc2.png) 13px center no-repeat;
    background-size: 10px 12px;
}
.car-index{
    height:154px;;
    position: relative;
    margin: 0 6px 7px 6px;
    text-align: left;
    height: 141px;
    overflow: hidden;
    padding: 3px 4% 10px 1.6%;
    background: url(/static/img/gc.png) 0 0 no-repeat;
    background-size: 100% 154px;
}
.car-tit{
    display: block;
    position: relative;
    z-index: 3;
    line-height: 44px;
    height: 147px;
    margin-bottom: -103px;
    color: #333;
    font-size: 15px;
    background: url(/static/img/m.png) right 17px no-repeat;
    background-size: 7px 11px;
    font-size: 14px;
    padding-left: 10px;
}
.car-gds{
    width:100%;
    display: flex;
}
.good-only{
    width:16%;
    margin-left: 10px;
    display: flex;
    flex-direction:column;
    align-items: center;
}
.good-only img{
    width:100%;
}
    
</style>




