import Vue from 'vue'
import Router from "vue-router"
import index from './components/index.vue'
import shop from './components/shop.vue'
import home from './components/home.vue'
import classify from './components/classify.vue'
import vfoot from './components/vfoot.vue'
import maincar from './components/maincar.vue'
import car from './components/car.vue'
import order from './components/order.vue'
import my from './components/my.vue'
import registuls from './components/registuls.vue'
import registx from './components/registx.vue'
import ElementUI from 'element-ui';
import loginx from './components/loginx.vue';
// import login from './components/login.vue';
import gddetail from './components/gddetail.vue';
Vue.use(Router)
// const router = new Router({
export default new Router({
    routes:[
        {
            path:"/",
            redirect:'/home'
        },
        {
            path:"/index",
            // component: {
            //     template:'<div>index</div>'
            // }
            component:index
        },
        {
            path:"/shop/:guid/:title/:minite/:freight/static/img/:imgURLS",
            component:shop,
            props:true
        },
        {
            path:"/home",
            components:{
                a:home,
                b:vfoot,
                c:index,
            }

        },
        {
            path:"/classify",
            components:{
                a:classify,
                b:vfoot,
            }
        },
        {
            path:"/vfoot",
            component:vfoot
        },
        {
            path:"/maincar",
            components:{
                a:maincar,
                b:vfoot,
            }
        },
        {
            path:"/order",
            components:{
                a:order,
                b:vfoot,
            }
        },
        {
            path:"/my",
            components:{
                a:my,
                b:vfoot,
            }
        },
        {
            path:"/registuls",
            components:{
                a:registuls,
                b:ElementUI,
            }
        },
        {
            path:"/gddetail",
            components:{
                a:gddetail,
                b:vfoot,
            }
        },
        {
            path:"/registx",
            components:{
                a:registx,
                b:vfoot,
            }
        },

    ]
})



