<template>
  <div>
    <div id="index">
    <ul class="shop-box">
      <div v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="10" infinite-scroll-throttle-delay="300">
        <li class="shoponly" v-for="(shop,index) in shops" :key="shop.guid">
          <span class="sname"><img :src="shop.imgURLS" alt=""></span>
          <!-- <img v-for="img in imgs"  alt=""> -->
          <div class="shopright">
              <div class="right1">
                <h2><router-link class="title" :to="{path:'/shop/'+shop.guid+'/'+shop.title+'/' +shop.minite+'/'+shop.freight+'/'+shop.imgURLS}">{{shop.title}}</router-link> <!-- //:to="/shop" -->
                </h2>
                <span class="send">达达专送-{{shop.minite}}分钟</span>
                <p class="sta"><span class="stars">{{shop.star}}</span>月售{{shop.sold}}单</p>
                <p class="text">{{shop.num}}元起送，距离{{shop.distance}}km,{{shop.freight}}</p>
            </div>
          <transition name="mybox">
            <div class="right2" >
              <p  class="shopsale" v-for="sale in shop.sales" :key="sale.guid" ><span class="ssname">{{sale.saleName}}</span><span class="txt">{{sale.id2}}</span></p>
              <i v-show="shop.sales.length>1" class="icox" @click="showSales(index)">&#xe600;</i>
              <!-- <i class="icox-up">&#xe617;</i> -->
              <p  class="stit"> <span>满减</span>立减2</p>
            </div>
          </transition> 
        </div> 
      </li>
    </div>  
    </ul> 
    </div>
  </div>
</template>
<script>
import infiniteScroll from "vue-infinite-scroll";
// import $ from 'juery'
export default {
  directives: {infiniteScroll:infiniteScroll},
  data() {
    return {
      busy: false,
      shops: [],
      imgURL:[],
      boxshow:true,
      count:2,

    };
  },
  created() {
        this.jQuery.get(this.apiURL + "/shops", result => {
          this.shops = result.data;
        });
  },
  methods: {
    loadMore() {
      // console.log("hello 1");
          this.busy = true;
          this.jQuery.get(this.apiURL + "/shops", result => {
            this.shops = this.shops.concat(result.data);
            this.busy = false;
          });
        },
      showSales(index){
        this.jQuery('.right2').eq(index).toggleClass('right3')
        this.jQuery('.icox').eq(index).toggleClass('icox2')
      }
    },
    mounted (){
        // console.log(this.jQuery('.shoponly').height())
    }
    // showAll:function(){
    //     this.count=this.partners.length
    //     //  this.boxshow = !this.boxshow;
    //   },
  }
  


</script>
<style>
#index{
  background: #fff;
}
.shop-box *{
  margin-bottom:0px;
  padding:0;
  /* line-height: 16px; */
  font-style: normal;
}
.shop-box{
  list-style: none;
}
.shoponly{
  padding:15px 10px;
  border-bottom:1px solid #e7e9e4;
  overflow: hidden;
}
.sname{
  display:inline-block;
  width:66px;
  height:66px;
  border:1px solid #E8E8E8;
}
.sname img{
  width:98%;
}
.shopright{
  width:78%;
  float: right;
}
.shopright p{
  margin-top: 6px;
}
.shopright .title{
  color:black;
  font-size:16px;
}
.send{
  font-size: 10px;
  color:#16A9FF;
  border:1px solid #16A9FF;
  padding:0 2px;
  display: inline-block;
    margin-top: 8px;
  /* box-sizing: border-box; */
}
.sta .stars{
  /* background: url('/static/img/star.png'); */
  display: inline-block;
  width:65px;
  height: 10px;
  background-size: auto 10px;
  color:#FFA14E;
  padding:0 2px;
  border-right: 1px solid #D9D9D9;
  line-height: 10px;
  margin-right: 3px;
}
.sta{
  font-size: 10px;
  color:#999;
}
.text{
  font-size: 10px;
  color:#999;
}
.right2{
  border-top:1px solid #e7e9e4;
  margin-top: 10px;
  position: relative;
  overflow: hidden;
  height:60px;
}
.right3{
  border-top:1px solid #e7e9e4;
  margin-top: 10px;
  position: relative;
  overflow: hidden;
  height:auto;
}
.ssname{
  display: inline-block;
  padding:3px;
  background: #FF6C57;
  font-size: 10px;
  line-height: 10px;
  color:#fff;
  border-radius: 3px;
  margin-right: 6px;
}
.stit,.txt{
  color:#999;
  font-size: 10px;
}
.stit span{
  display: inline-block;
  padding:3px;
  background: #5FBC65;
  font-size: 10px;
  line-height: 10px;
  color:#fff;
  border-radius: 3px;
  margin-right: 6px;
}
.icox{
  float:right;
  font-family: iconfont;
  /* margin-top:10px; */
  color:#999;
  /* display:none; */
  position: absolute;
  right:10px;
  top:10px;

}
.icox2{
  float:right;
  font-family: iconfont;
  /* margin-top:10px; */
  color:#999;
  /* display:none; */
  position: absolute;
  right:10px;
  top:10px;
  transition: 0.1s;
  transform:rotate(180deg);
}
.mybox-leave-active,.mybox-enter-active{
            transition:  all 1s ease; 
        }
        .mybox-leave-active,.mybox-enter{
            height:50px !important;
        }
        .mybox-leave,.mybox-enter-active{
            height: 150px;
        }
</style>




