<script>
import car from './car.vue'
export default {
  props: ["guid", "title","minite","freight","imgURLS"],
  data() {
    return {
      ctgs: [],
      goods: [],
      ctgText: "",
      count: "",
      flags:false,
      // show:false,
    };
  },
  components:{
      car,
  },
  created() {
    //初始化购物车
    // this.bus.cars[this.guid]=[];
    //  console.log(this.bus.cars)   //直接调取cars
    if (!this.bus.cars[this.guid]) {
      this.$set(this.bus.cars, this.guid, []); //完成双向绑定
    }
    this.jQuery.get(this.apiURL + "/ctgs", result => {
      if (result.errcode == 0) {
        this.ctgs = result.data;
      }
    });
    
  },
  methods: {
    bigshow(){
      this.jQuery('.shop-dshow').toggle()
    },
    //点击收藏变色
    coll(){
      this.jQuery('.collect').toggleClass('collect1')
    },
    //点击继续购物，展示商品页
    ups(){
      this.jQuery('.shop-youhui').slideUp()
      this.jQuery('#shop').toggle()
    },
    //点击出现店铺信息
    showMain(){
      var _this=this
      this.jQuery('.shop-youhui').slideToggle()
      // _this.flags= !_this.flags
      this.jQuery('#shop').toggle()
    },
    //点击出现二级分类
    showKind(index){
      this.jQuery('.second-kind').eq(index).slideToggle()
      this.jQuery('.second-kind').not(this.jQuery('.second-kind').eq(index)).slideUp()
      // ctg.show=!ctg.show
      // this.ctgs.splice(index,1,ctg)
      //点击变色
      this.jQuery('.kind').eq(index).addClass('solid').parent().siblings().find('.kind').removeClass('solid')
      this.jQuery('.kind').eq(index).addClass('solid').parent().siblings().removeClass('solid')
      this.jQuery('.kind').eq(index).addClass('solid').find('.li-a').removeClass('solid')
    },
  //商品展示
    showGoods(text,index,event) {
      this.jQuery.get(this.apiURL + "/goods", { ctgtext: text }, result => {
        if (result.errcode == 0) {
          this.goods = result.data;
          this.ctgText = result.ctgText;
          this.count = result.count;
        }
      });
      this.jQuery('.li-a').removeClass('solid')
      this.jQuery(event.target).addClass('solid').parent().parent().siblings('p').removeClass('solid')
    },
   //添加商品
    addCar(gd) {
      var car = this.bus.cars[this.guid];
      for (var i = 0; i < car.length; i++) {
        if (car[i].guid == gd.guid) {
          
          if (car[i].num < gd.store) {
              car[i].num++;
              //重新双向绑定
              car.splice(i,1,car[i])
            // num = gd.store;
          this.$set(this.bus.carnums,gd.guid,car[i].num);
          }
          // console.log(this.bus.cars)
          return;
        }
      }
      gd.num = 1; //商品的数量
      if (gd.num >= gd.store) {
        gd.num = gd.store;
      }
      this.bus.cars[this.guid].push(gd);
      console.log(this.bus.cars);
      this.$set(this.bus.carnums, gd.guid, 1);
    },
    //减除商品
    subCar(gd){
         var car = this.bus.cars[this.guid];
      for (var i = 0; i < car.length; i++) {
        if (car[i].guid == gd.guid) {
          var num = --car[i].num;
          car.splice(i,1,car[i])
          if (num < 0) {
            delete this.bus.carnums[gd.guid]  //删除对象
            this.bus.cars[this.guid].splice(i,1)
          }
          this.$set(this.bus.carnums, gd.guid, num);
          // console.log(this.bus.cars)
          return;
        }
      }
    }
  },
  //默认展示商品
  mounted(){
    this.jQuery.get(this.apiURL + "/goods", result => {
        if (result.errcode == 0) {
          this.goods = result.data;
          this.ctgText = result.ctgText;
          this.count = result.count;
        }
      });
  }

};
</script>
