    background-image: url(/static/img/12.png);
        background-repeat: no-repeat;
    background-size: 220px auto;
}

.solid{
    border-left: 3px solid #75C67B;
    background: #fff;
    border-right: 1px solid #fff;
    color:#75C67B;
}
#shop-main{
  width:100%;
  background: url('/static/img/111.jpg');
  background-size: 100% auto;
  /* position:fixed; 
  top:0;
  left:0;  
    
  /* overflow: auto;
  height:100%; */
  padding:0 10px;
  box-sizing: border-box;
  z-index:3000;
}
.shop-youhui{
    /* height:100%;
    overflow: auto; */
    display: none;
}
.shop-header{
  width:100%;
  height:90px;
  padding:10px;
  box-sizing: border-box;
  overflow: hidden;
}
.collect{
    position: absolute;
    right:10px;
    top:16px;
    width: 22px;
    height: 18px;
    /* width: 50px;
    height: 50px; */
    background-image: url(/static/img/shou.png);
    background-repeat: no-repeat;
    background-position: 0px -2px;
    /* background-position: 0px -36px; */
    -webkit-background-size: 60px auto;
    background-size: 60px auto;
    margin-left: 12px;
}
.collect1{
    position: absolute;
    right:10px;
    top:16px;
    width: 22px;
    height: 18px;
    background-image: url(/static/img/shou.png);
    background-repeat: no-repeat;
    background-position: 0px -42px;
    -webkit-background-size: 60px auto;
    background-size: 60px auto;
    margin-left: 12px;
}
.shop-message{
  display: block;
  position: absolute;
  right:10px;
  top:50px;
  font-size: 10px;
  background: rgba(70,70,70,0.5);
  width: 54px;
    height: 16px;
    line-height: 14px;
    text-align: center;
    border-radius: 4px;
}
.stext{
  color:#fff;
  line-height: 20px;
}
.stext p{
  font-size: 10px;
}
.stext h3{
  font-weight: 500;
  margin-bottom: 0;
}
.slogo{
  width:16%;
  border:1px solid #fff;
  border-radius: 3px;
  overflow: hidden;
  margin-right: 10px;
}
.slogo img{
  width:100%;
}
.shop-header>div{
  float: left;
}
#shop{
  overflow: hidden;
  width:100%;
  height:100%;
  background: #F8F8F8;
  position: fixed;
  left:0;
  top:108px;

}
.shop-search{
  width:100%;
  height:40px;
  overflow: hidden;
  background: #EAEAEA;
}
.shop-sear{
  width:96%;
  height:30px;
  background: #fff;
  margin: 5px auto;
  border-radius: 3px;
  font-size: 14px;
  color:#A8A8A8;
  line-height: 30px;
}
.shop-sear i{
  font-family: iconfont;
  margin-right: 10px;
  margin-left: 10px;
}
.dw-left,.right {
  float: left;

}
#shop .dw-left {
  width: 22%;
  margin-top: 5px;
  height:100%;
  background: #F4F4F4;
  overflow: auto;

}
.dw-left ul{
  width:100%;
}
.kind{
  /* height:40px; */
  line-height: 44px;
  border-bottom: 1px solid #e8e8e8;
  border-right: 1px solid #e8e8e8;
  padding-left: 12px;
  box-sizing: border-box;
  font-size: 14px;
}
.minli{
    height:44px;
    font-size: 14px; 
    line-height: 44px;
   border-bottom: 1px solid #e8e8e8;
   border-right: 1px solid #e8e8e8;
  
}
.minli .li-a{
  color:#000;
  text-decoration: none;
  display: inline-block;
  width:100%;
  height:100%;
  padding-left: 12px;
  box-sizing: border-box;
  
}
.second-kind li{
  width:100%;
  height: 43px;
  line-height: 44px;

 }
 .second-kind{
   /* overflow: hidden; */
   width:100%;
   background: #fff;
   display: none;
 }

#shop .dw-right{
  width: 78%;
  height:100%;
  overflow: auto;
}
.dw-right h2{
  width:100%;
  height: 30px;
  background: #F4F4F4;
  padding-left: 11px;
  box-sizing: border-box;
  font-size: 14px;
  line-height: 30px;
  font-weight: 500;
  margin-bottom: 0;
  /* position:fixed;
  top:148px;
  left:80px; */
  z-index: 100;
}

.right-li{
  border-bottom:1px solid #ddd;
  width:100%;
  overflow: hidden;
  background: #fff;
  padding:10px 10px 10px 0;
  

}
.right-li > div{
  float: left;
}
.li-img{
  width:30%;
  text-align: center;
}
.li-img img{
  width:78%;
}
.li-text{
  width:70%;
  height:100%;
  position: relative;
  
}
.li-text h3{
  font-weight: 500;
  margin-bottom: 0;
  font-size: 14px;
}
.gd-sold{
  color:#999;
  font-size: 12px;
}
.qwe{
  display:block;
  width:30px;
  padding:0 2px;
  box-sizing: border-box;
  font-size: 12px;
  background: #FFCE0B;
  color:#fff;
  border-radius: 3px;
}
.gdprice{
  width:100%;
  overflow: hidden;
  padding:5px;
  height:30px;
  box-sizing: border-box;
  font-size: 14px;

}
.gdprice span:nth-child(1){
  color:red;
  margin-right: 10px;
  height:30px;
  display: block;
  float: left;
  
} 
.gdprice del{
  color:#9F9F9F;
  text-decoration: line-through;
  width:47px;
  height:30px;
  float: left;
  display: block;
  overflow: hidden;
} 
.gd-add,.gd-minus{
  display: inline-block;
  width:28px;
  height:28px;
  line-height: 20px;
  text-align: center;
  color:#47B34F;
  font-size: 28px;
  font-weight: 800;
  border:1px solid #E8E8E8;
  /* box-sizing: border-box;*/
  border-radius: 50%;
}
.gd-text{
  display:inline-block;
  width:24px;
  height:24px;
  font-size: 12px;
  text-align: center;
  padding-bottom:15px;
  box-sizing: border-box;
}
.go{
  position: absolute;
  bottom:-2px;
  right:-5px;
}
.cover{
  width:100%;
  height:100%;
  position:fixed;
  top:0;
  left:0;
  background: rgba(0,0,0,0.7);
  display:none;
  z-index: 1;
}
</style>




