<template>
  <div id="car">
      <!-- <button @click="emptyCar">清空购物车</button> -->
      <div class="shopcart">
         
      <!-- 购物车商品 -->
      <ul>
            <div class="all">
                <div class='checkboxall'> 
                    <input type='checkbox' id='checkboxall' v-model="allchecked" name='checkboox1[]'>
                    <label for='checkboxall' class="labelbox1" @click="checkedbox1()"></label> 
                </div>
                <span>全选(已选{{bus.cars[shopid].length}}件)</span>
                <div class="empty" @click="emptyCar">清空购物车</div>
            </div>
          <li class="car-li" v-for="(car,index) in bus.cars[shopid]" :key="car.guid">
              <!-- 全选 -->
              
              <!-- checkbox -->
            <div class="car-checkbox">
                <input type='checkbox' :id='index' name='checkboox[]' v-model="checked" :value="car.guid"><label :for='index' @click="checkbox(car.guid)"></label>
            </div>
            <!-- 商品信息 -->
              <table class="car-good" >
                <tr>
                    <td style="width: 62px;">
                        <div class="car-gdimg">
                            <img :src="car.goodImg" alt="">
                        </div>
                    </td>
                    <td>
                        <div class="car-title">{{car.text}}</div>
                        <div class="gd-price">&yen;{{car.price}}</div>
                    </td>
                </tr>
                <div class="car-go">
                <span class="gd-minus" @click="subCar(car)">-</span>
                {{car.num}}
                <span class="gd-add" @click="addCar(car)">+</span>
            </div>
            </table>
            
          </li>
      </ul>
      <!-- 当购物车为空时 -->
        <!-- <div class="iconcar"  v-if="bus.cars[this.shopid].length<=0">&#xe606;</div> -->
        <div class="emptycar" v-if="bus.cars[this.shopid].length<=0">
            <span class="emptycar-icon"></span>
            <p class="car-tip">购物车是空的</p> 
            <p class="car-cash">去结算</p>
        </div>
      <!-- <div>&yen;<b>{{total}}</b></div> -->
         <!-- 有商品时状态  -->
         <div class="car-bot" v-if="bus.cars[this.shopid].length>0">
            <i class="gdnum">{{totalnum}}</i>
                <span class="emptycar-icon1" @click="gdShow"></span>
            <div class="payment" >
                <p class="car-tip1">&yen;&nbsp;<b>{{total}}</b></p>  
                <p class="car-cash1">去结算</p>
            </div>
        </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["shopid"],
  data() {
    return {
        
      checked: [],
      allchecked: false,
      n: 0,
      m: 0
    };
  },
  computed: {
    total() {
      var sum = 0;
      var shopid = this.bus.cars[this.shopid];
      for (var i = 0; i < shopid.length; i++) {
        var gid = this.bus.cars[this.shopid][i].guid;
        if (this.checked.indexOf(gid) == -1) {
          continue;
        }
        sum += shopid[i].price * shopid[i].num;
        sum = Math.round(sum * 100) / 100;
      }
      return sum;
    },
    //添加商品的总量
    totalnum() {
      var sum = 0;
      for (let i = 0; i < this.bus.cars[this.shopid].length; i++) {
        sum += this.bus.cars[this.shopid][i].num;
      }
      return sum;
    }
  },

  methods: {
    //全选
    checkedbox1() {
      for (let i = 0; i < this.bus.cars[this.shopid].length; i++) {
        var gid = this.bus.cars[this.shopid][i].guid;
        if (this.m % 2 == 0) {
          if (this.checked.indexOf(gid) == -1) {
            this.checked.push(gid);
          }
        } else {
          this.checked = [];
        }
      }
      this.m++;
    },
    //单选
    checkbox(id) {
      let index = this.checked.indexOf(id);
      if (index >= 0) {
        this.checked.splice(index, 1);
      } else {
        this.checked.push(id);
      }
      if (this.checked.length == this.bus.cars[this.shopid].length) {
        this.allchecked = true;
        this.m = 1;
      } else {
        this.allchecked = false;
        this.m = 0;
      }
    },
    //点击购物车，出现购物车的商品
    gdShow() {
      this.jQuery("#car ul").slideToggle();
      this.jQuery(".cover").toggle();
      if (this.jQuery(".emptycar-icon1").css("top") == "-5px") {
        this.jQuery(".emptycar-icon1").css({ top: "-52px" });
        this.jQuery(".car-tip1").css({ left: "10px" });
        this.jQuery(".gdnum").css({ top: "-58px" });
      } else if (this.jQuery(".emptycar-icon1").css("top") == "-52px") {
        this.jQuery(".emptycar-icon1").css({ top: "-5px" });
        this.jQuery(".car-tip1").css({ left: "85px" });
        this.jQuery(".gdnum").css({ top: "-10px" });
      }
    },
    //清空购物车
    emptyCar() {
      this.jQuery(".emptycar-icon1").css({ top: "-5px" });
      this.jQuery(".car-tip1").css({ left: "60px" });
      this.jQuery(".shopcart ul").slideToggle();
      this.jQuery(".cover").toggle();
      //清空购物车
      this.$set(this.bus.cars, this.shopid, []);
      this.$set(this.bus, "carnums", {});
    },
    addCar(gd) {
      //添加商品
      var car = this.bus.cars[this.shopid];
      for (var i = 0; i < this.bus.cars[this.shopid].length; i++) {
        if (car[i].guid == gd.guid) {
          if (car[i].num <= gd.store) {
            car[i].num++;
            //
            car.splice(i, 1, car[i]);
            // num = gd.store;
            this.$set(this.bus.carnums, gd.guid, car[i].num);
          }
          // console.log(this.bus.cars)
          return;
        }
      }
      gd.num = 1; //商品的数量
      if (gd.num >= gd.store) {
        gd.num = gd.store;
      }
      this.bus.cars[this.shopid].push(gd);
      // console.log(this.bus.cars);
      this.$set(this.bus.carnums, gd.guid, 1);
    },
    subCar(gd) {
      var car = this.bus.cars[this.shopid];
      for (var i = 0; i < car.length; i++) {
        if (car[i].guid == gd.guid) {
          var num = --car[i].num;
          car.splice(i, 1, car[i]);
          if (num < 1) {
            delete this.bus.carnums[gd.guid]; //删除对象
            car.splice(i, 1);
          }
          if (this.bus.cars[this.shopid].length == 0) {
            this.jQuery(".cover").css({ display: "none" });
            this.jQuery(".emptycar-icon1").css({ top: "-5px" });
            this.jQuery(".car-tip1").css({ left: "60px" });
            this.jQuery("#car ul").css({ display: "none" });
          }
          this.$set(this.bus.carnums, gd.guid, num);
          // console.log(this.bus.cars)
          return;
        }
      }
    }
  }
};
</script>
<style>
#car {
  background: #fff;
  position: fixed;
  bottom: 0;
  width: 100%;
  font-family: "Microsoft YaHei", Verdana, Arial, Helvetica, sans-serif;
  z-index: 1000;
}
.car-li {
  overflow: hidden;
  padding: 0 0 5px 0;
  box-sizing: border-box;
  position: relative;
}

.car-good {
  float: right;
  width: 90%;
}
.car-good tr {
  padding-bottom: 20px;
  box-sizing: border-box;
  overflow: hidden;
  height: 83px;
  border-bottom: 1px solid #ddd;
  padding-left: 10px;
}
.car-good td {
  height: 62px;
}
.car-checkbox {
  /* display: inline-block;*/
  position: relative;
  height: 35px;
  width: 35px;
  position: absolute;
  top: 28px;
  left: 15px;
  margin-right: 10px;
}
.car-checkbox input[type="checkbox"] {
  position: absolute;
  left: 0;
  top: -4px;
  width: 20px;
  height: 20px;
  opacity: 0;
}
.car-checkbox label {
  position: absolute;
  left: 24px;
  top: 0;
  height: 20px;
  line-height: 20px;
}
.car-checkbox label:before {
  content: "";
  position: absolute;
  left: -30px;
  top: 0;
  width: 20px;
  height: 20px;
  border: 1px solid #ddd;
  border-radius: 50%;
  transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
}
.car-checkbox label:after {
  content: "";
  position: absolute;
  left: -22px;
  top: 3px;
  width: 6px;
  height: 12px;
  border: 0;
  border-right: 1px solid #fff;
  border-bottom: 1px solid #fff;
  background: #fff;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
}
.car-checkbox input[type="checkbox"]:checked + label:before {
  background: #4cd764;
  border-color: #4cd764;
}
.car-checkbox input[type="checkbox"]:checked + label:after {
  background: #4cd764;
}

.car-gdimg {
  width: 58px;
  height: 58px;
  margin-right: 16px;
}
.car-gdimg img {
  width: 100%;
}
.car-title {
  font-size: 13px;
}
.gd-price {
  color: red;
  margin-top: 12px;
}
.car-go {
  position: absolute;
  bottom: 16px;
  right: 15px;
}
.emptycar {
  width: 100%;
  height: 49px;
  display: flex;
  align-items: center;
  /* margin-top: -15px; */
}
.emptycar-icon {
  display: inline-block;
  width: 70px;
  height: 60px;
  background: url("/static/img/cart.png") 10px 7px no-repeat;
  background-size: 50px 125px;
  margin-left: 10px;
  margin-right: 20px;
  /* font-family: iconfont;*/
  /* background: #BEBEBE; */
  /* border-radius: 50%; */
  /* color:#fff;
    text-align: center;
    line-height: 40px;
    font-size: 24px; */
}
.car-tip {
  color: #999999;
  font-size: 15px;
  width: 100px;
}
.car-cash {
  width: 115px;
  height: 48px;
  text-align: center;
  line-height: 48px;
  color: #fff;
  font-size: 15px;
  background: #bebebe;
  position: absolute;
  bottom: 0;
  right: 0;
}
#car ul {
  display: none;
  max-height: 400px;
  overflow: auto;
  width: 100%;
  margin-bottom: 0;
  background: #fff;
  border: 1px solid #fff;
}
.payment {
  width: 100%;
  height: 49px;
  /* display: flex; */
  /* align-items: center; */
  border-top: 1px solid #e3e3e3;
  background: #fff;
  position: relative;
}
.emptycar-icon1 {
  display: inline-block;
  width: 60px;
  height: 70px;
  background: red;
  background: url("/static/img/cart.png") 6px -74px no-repeat;
  background-size: 50px 125px;
  margin-right: 20px;
  z-index: 200;
  position: absolute;
  top: -5px;
  left: 10px;
}
.emptycar-icon2 {
  /* position: absolute;
    top:-50px;
    left:10px; */
}
.car-tip1 {
  color: #ff5353;
  font-size: 18px;
  width: 80px;
  position: absolute;
  top: 10px;
  left: 90px;
}
.car-cash1 {
  width: 115px;
  height: 48px;
  text-align: center;
  line-height: 48px;
  color: #fff;
  font-size: 15px;
  background: #47b34f;
  position: absolute;
  bottom: 0;
  right: 0;
}
.gdnum {
  position: absolute;
  top: -12px;
  left: 56px;
  padding: 0 5px;
  height: 15px;
  line-height: 15px;
  overflow: hidden;
  text-align: center;
  color: #fff;
  background-color: #ff5d5c;
  border-radius: 7px;
  font-size: 10px;
  z-index: 202;
}
.gdnum1 {
  position: absolute;
  top: -59px;
  left: 56px;
  padding: 0 5px;
  height: 15px;
  line-height: 15px;
  overflow: hidden;
  text-align: center;
  color: #fff;
  background-color: #ff5d5c;
  border-radius: 7px;
  font-size: 10px;
  z-index: 202;
}
.shopcart {
  position: relative;
  width: 100%;
  border: 1px solid #fff;
}
.all {
  width: 100%;
  height: 40px;
  border-bottom: 1px solid #ddd;
}
.checkboxall {
  position: absolute;
  height: 60px;
  width: 50px;
  top: -15px;
  left: 0px;
}
.checkboxall input[type="checkbox"] {
  position: absolute;
  left: 0px;
  top: 0px;
  width: 20px;
  height: 20px;
  opacity: 0;
}
.checkboxall label {
  position: absolute;
  left: 30px;
  top: 6px;
  height: 20px;
  line-height: 20px;
}
.checkboxall label:before {
  content: "";
  position: absolute;
  left: -20px;
  top: 20px;
  width: 20px;
  height: 20px;
  border: 1px solid #ddd;
  border-radius: 50%;
  transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
}
.checkboxall label:after {
  content: "";
  position: absolute;
  left: -13px;
  top: 23px;
  width: 6px;
  height: 12px;
  border: 0;
  border-right: 1px solid #fff;
  border-bottom: 1px solid #fff;
  background: #fff;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
}
.checkboxall input[type="checkbox"]:checked + label:before {
  background: #4cd764;
  border-color: #4cd764;
}
.checkboxall input[type="checkbox"]:checked + label:after {
  background: #4cd764;
}
.all span {
  margin-top: 5px;
  margin-bottom: 5px;
  display: inline-block;
  font-size: 14px;
  margin-left: 40px;
}
.empty {
  text-align: right;
  color: #666;
  font-size: 14px;
  float: right;
  margin-right: 10px;
  margin-top: 5px;
}
</style>




