<template>
    <div>
        <div id="gddetails" v-for="detail in gddetail" :key="detail.guid">
            <div class="swiper-container swipers6">
                <div class="swiper-wrapper">
                    <div class="swiper-slide swiperc6" v-for="oneimg in detail.gdimg" :key="oneimg.guid"><img :src="oneimg.pics" alt=""></div>
                </div>
                <div class="swiper-pagination pagination1"></div>
            </div>
            <div class="hr_shadow"></div>
            <div class="de-tits">
                {{detail.text}}
            </div>
            <div class="g-price">
                <span class="e12">￥</span>
                <span class="bigprice">{{detail.Tprice}}</span>
                <del class="delprice">&yen;{{detail.Tprice+5}}</del>
                <span class="ad-car">加入购物车</span>
            </div>
            <div class="clearfix">
                <i class="weid"></i>
                <span class="shop-tits">{{detail.tit}}</span>
                <i class="dw"></i>
                <span class="lian">联系商家</span>
            </div>
            <div class="ssdw">
                <p>商品评价<span>共({{detail.num}})人评价</span></p>
                <div class="lsd">
                    <div class="dolt"></div>
                    <div class="heng"></div>
                    <div class="god">好评{{detail.num}}%</div>
                </div>
            </div>
        </div>
        <div class="cover"></div>
      <div class="shop-ert" @click="bigshow"></div>
      <div class="shop-dshow">
        <a href="#/home"><div class="big-t">
          <i>&#xe66f;</i><span>首页</span>
        </div>
        </a>
        <a href="#/classify"><div class="big-t">
          <i>&#xe634;</i><span>分类</span>
        </div>
        </a>
        <a href="#/miancar">
        <div class="big-t">
          <i>&#xe636;</i><span>购物车</span>
        </div>
        </a>
        <a href="#/order">
        <div class="big-t">
          <i>&#xe649;</i><span>订单</span>
        </div>
        </a>
        <a href="#/my">
        <div class="big-t">
          <i>&#xe613;</i><span>我的</span>
        </div>
        </a>
      </div>
    </div>
</template>
<script>
import 'swiper/dist/css/swiper.css'
import Swiper from 'Swiper'
export default {
  data() {
    return {
      gddetail: []
    };
  },
  created() {
    this.jQuery.get(this.apiURL + "/gddetail", result => {
      if (result.errcode == 0) {
        this.gddetail = result.data;
      }
      //  console.log(this.classify)
    });
  },
  methods: {
    bigshow(){
      this.jQuery('.shop-dshow').toggle()
    },
  },
  updated(){
      var mySwiper6 = new Swiper ('.swipers6', {
        direction: 'horizontal',
        autoplay:true,
        loop: true,
        pagination: {
            el: '.swiper-pagination',
            type: 'fraction',
        }
    })
  }
};
</script>
<style>
body{
    background: #f4f4f4;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  word-wrap: break-word;
  font-size: 14px;
}
.ssdw{
    background: #ffffff;
    height:70px;
    position: relative;
}
.ssdw p{
    padding:5px 16px;
}
.ssdw p span{
    color:#999;
    margin-left: 6px;
    font-size: 13px;
}
.heng{
    width:75%;
    height:2px;
    background-color: #e8e8e8;
    margin-left: 20px;
}
.dolt{
    display: inline-block;
    position: absolute;
    left: 13px;
    top: 42px;
    border:2px solid #47b34f;
    height: 8px;
    width:8px;
    border-radius: 50%;
}
.god{
    position: absolute;
    color:#47b34f;
    right:20px;
    top: 32px;
    font-size: 13px;
}
#gddetails{
    position: relative;
}
.swipers6 {
    width: 375px;
    height: 375px;
}  
.de-img {
  position: relative;
  display: inline-block;
  vertical-align: top;
  width: 100%;
  text-align: center;
}
.swiperc6{
    text-align: center;
}
.swiperc6 img{
    width:100%;
}
.pagination1{
    display: inline-block;
    background-color: rgba(0,0,0,0.4);
    height: 24px;
    line-height: 24px;
    margin-left: 310px;
    width: 50px;
    border-radius: 12px;
    text-align: center;
    color: #ffffff;
    font-size: 22px;
}
.hr_shadow {
    width: 100%;
    height: 8px;
    overflow: hidden;
    background: linear-gradient(left,#ffffff 0%,#d4d3d3 49%,#d4d3d3 50%,#ffffff 100%);
    box-shadow: inset 0px 8px 5px rgba(255,255,255,0.8);
}
.de-tits{
    width:100%;
    height:31px;
    background: #fff;
    text-align: center;
    font-size: 16px;
    padding: 0 20px;
    color: #333;
    line-height: 40px;
    text-overflow: ellipsis;
    overflow: hidden;
    max-height: 44px;
}
.g-price{
    height:46px;
    background: #fff;
    padding: 0 16px;
    line-height: 40px;
    border-bottom: 1px solid #E2E2E2;
}
.e12{
    font-size: 20px;
    color: #ff3434;
    margin-right: -5px;
}
.bigprice{
    display: inline-block;
    font-size: 30px;
    color: #ff3434;
    margin-right: 5px;
}
.delprice{
    color:#B2B2B2;
}
.ad-car{
    width:90px;
    height:33px;
    background: #47B34F;
    color:#fff;
    text-align: center;
    line-height: 33px;
    float:right;
    margin-top: 5px;
    margin-left: -5px;
}
.clearfix{
    height:46px;
    background: #fff;
    padding: 5px 16px;
    margin-top: 10px;
    line-height: 38px;
    position: relative;
    border-bottom: 1px solid #E2E2E2;
}
.weid{
    background:url('/static/img/gf.png') 20px 2px no-repeat;
    background-size: 200px 150px;
    background-position: -44px 4px;
    display: inline-block;
    width:20px;
    height:20px;
    position: absolute;
    top:13px;
    left:10px;
    
}
.shop-tits{
    font-size: 13px;
    display: inline-block;
    width:55%;
    border-right: 1px solid #E2E2E2;
    height:25px;
    line-height: 25px;
    margin-left: 20px;
}
.dw{
    background:url('/static/img/gf.png') no-repeat;
    background-size: 200px 150px;
    background-position: -24px 4px;
    display: inline-block;
    width:20px;
    height:20px;
    position: absolute;
    top:13px;
    right:76px;
}
.lian{
    margin-left: 70px;
}
.shop-ert{
    position: fixed;
    z-index: 300;
    right: 0;
    bottom: 100px;
    width: 38px;
    height: 38px;
    background: rgba(0,0,0,0.8) url(/static/img/bm.png) 7px 7px no-repeat;
    background-size: 65px 20px;
    border: 1px solid #757575;
    border-radius: 4px 0 0 4px;
}
.shop-dshow{
  position: fixed;
    z-index: 1000;
    right: 0;
    bottom: 140px;
    width: 95px;
    background: rgba(0,0,0,0.8);
    display: none;
}
.shop-dshow div{
  width: 95px;
  height: 36px;
  color:#fff;
  border-bottom:1px solid #3E3E3E;
  font-size: 14px;
  padding:10px;
  box-sizing: border-box;
}
.shop-dshow div i{
  font-family: iconfont;
  margin-right: 16px;
}
</style>




