    return {
        
    };
  },
  created() {
        var $ = this.jQuery;
        $(window).scroll(function(event) {
        //   console.log($(this).scrollTop());
        var top = $(this).scrollTop();
        var al = top / 106;
        if (al > 1) {
            return;
        }
        $(".search").css("background", "rgba(75,199,84, " + (1 - al) + ")");
        $(".topnav").css("background", "rgba(75,199,84, " + al + ")");
        if (top < 10) {
            $(".right").css({
            marginRight: "-40px",
            transition: "1s"
            });
        } else {
            $(".right").css({
            marginRight: "0px",
            transition: "1s",
            
            });
        }
        });
        this.jQuery.get(this.apiURL + "/index", result => {
        this.shops = result.data;
    });
    },
 components:{
    //   index,
    //   vfoot,
  },
  mounted(){
      var mySwiper1 = new Swiper ('.swipers1', {
        direction: 'horizontal',
        autoplay:true,
        loop: true,
        pagination: {
            el: '.swiper-pagination',
        }
    })
        var mySwiper2 = new Swiper('.swipers2',{
        loop: false,
        slidesPerView: 'auto',
        loopedSlides: 19,
        freeMode : true,
        scrollbar: {
         el: '.swiper-scrollbar',
         },
    })
    var mySwiper3 = new Swiper('.swipers3',{
        direction: 'horizontal',
        autoplay:true,
        loop: true,
        pagination: {
