<template>
    <div>
        <div id="loginx">
            <div class="reg-tit">
                <i class="reg-icon"></i>
                京东登录
            </div>
            <div class="lgx-text">
                <div class="lg-left" >登录</div>
                <div class="lg-right" @click="lxShow">注册</div>
            </div>
        </div>
    </div>
</template>
<script>
     export default {
        data(){
            return {
                regShow:true,
            }
        },
        methods:{
            lxShow(){
                this.regShow = !this.regShow
            }
        }
     }
</script>
<style>
* {
    padding: 0px;
    margin: 0px;
    font-size: 14px;
    box-sizing: border-box;
}
.reg-tit{
    height: 45px;
    line-height:45px;
    background: #f9f9f9;
    color: #616161;
    font-size: 17px;;
    border-bottom: 1px solid #bbb;
    text-align: center;
    /* z-index: 999; */
}
.reg-icon{
    background:url(/static/img/iconBg.png) 0 -61px;
    width: 22px;
    height: 22px;
    display: inline-block;
    background-repeat: no-repeat;
    background-size: 100% auto;
    position: absolute;
    top:10px;
    left:12px;
}
.lgx-text{
    line-height: 44px;
    font-size: 14px;
    border-bottom: 1px solid #d7d7d7;
    display: flex;
    justify-content: space-around;
}
.lgx-text>div{
    padding:0 20px;
    height:100%;
    line-height: 44px;
    border-bottom: 2px solid #F23030;

}

</style>




