<template>
  <div>
      <div id="logq">
      <div id="loginx">
            <div class="reg-tit">
                <router-link to="/my"><i class="reg-icon"></i></router-link>
                京东登录
            </div>
            <div id="lgx-text">
                <div class="lg-leftv" @click="lxShow" >登录</div>
                <div class="lg-rightv" @click="lxShow1">注册</div>
            </div>
        </div>
        <div id="login" v-show="!regShow">
            <div class="log-main">
                <div class="lgname">
                    <span>账号</span>
                    <input type="text" placeholder="用户名/邮箱/已验证手机">
                    <i class="icon-del"></i>
                </div>
                <div class="lgname">
                    <span>密码</span>
                    <input type="password" placeholder="请输入密码">
                    <i class="icon-del1"></i>
                </div>
                <div class="lg-btn">登 录</div>
                <div class="lg-btw">一键登录</div>
                <div class="lg-tip">
                    <span>
                        <i class="tip-left"></i>
                        <span>忘记密码</span>    
                    </span>
                    <span>
                        <i class="tip-right"></i>
                        <span>手机快速注册</span>    
                    </span>
                </div>
            </div>
        </div>
      <div id="regist" v-show="regShow">
          <div class="reg-main">
                <el-form id="regFrm" :status-icon="true" :rules="rules" label-position="left" action="" method="" ref="form" :model="form"
                    label-width="70px">
                    <el-form-item label="用户名" prop="username">
                        <el-input v-model="form.username" name="username"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" prop="password">
                        <el-input type="password" v-model="form.password" name="password"></el-input>
                    </el-form-item>

                    <el-form-item label="确认密码" prop="password2">
                        <el-input type="password" v-model="form.password2"></el-input>
                    </el-form-item>

                    <el-form-item label="邮箱" prop="email">
                        <el-input v-model="form.email" name="email"></el-input>
                    </el-form-item>
                    <el-form-item label="手机" prop="phone">
                        <el-input v-model="form.phone" name="phone"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button id="reg-btn" @click="regist()" type="primary">立即注册</el-button>
                    </el-form-item>
                </el-form>
            </div>
      </div>
      <div class="lg-space">
        <h4>其他登录方式</h4>
        <i class="other"></i>
        <p>登录即代表您已同意京东隐私政策</p>
    </div>
    </div>

    <div class="agreement-dialog" v-show="covershow">
    <div class="agreement-body">
        <p class="agreement-title">注册协议及隐私政策</p>
        <div class="agreement-line clearfix"></div>
        <div class="agreement-content">
            <p>在您注册成为京东用户的过程中，您需要完成我们的注册流程并通过点击同意的形式在线签署以下协议，<span class="important-tip">请您务必仔细阅读、充分理解协议中的条款内容后再点击同意（尤其是以粗体并下划线标识的条款，因为这些条款可能会明确您应履行的义务或对您的权利有所限制）：</span></p>
            <p class="link-top"><a href="">《京东用户注册协议》</a></p>
            <p class="link-content"><a href="">《用户隐私政策》</a></p>
            <p class="link-bottom"><a href="">《订单共享与安全》</a></p>
            <p><span class="important-tip">【请您注意】如果您不同意上述协议或其中任何条款约定，请您停止注册。您停止注册后将仅可以浏览我们的商品信息但无法享受我们的产品或服务。如您按照注册流程提示填写信息、阅读并点击同意上述协议且完成全部注册流程后，即表示您已充分阅读、理解并接受协议的全部内容；并表明您也同意京东可以依据以上的隐私政策内容来处理您的个人信息。</span>如您对以上协议内容有任何疑问，您可随时与京东客服联系。</p>

        </div>
        <div class="agreement-btn-wrap">
            <p class="agreement-notice"><span>点击同意即表示您已阅读并同意<a href="">《京东用户注册协议》</a>与<a href="">《京东隐私政策》</a>并将您的订单信息共享给为完成此订单所必须的第三方合作方。关于<a href="">《订单共享与安全》</a></span></p>
            <div class="agreement-btns">
                <a href="#/registx" class="agreement-btn btn-cancel agreement-btn-cancel clearfix">不同意</a>
                <div @click="covershows" class="agreement-btn btn-complete clearfix">同意</div>
            </div>
        </div>
    </div>
</div>
  </div>
</template>
<script>
export default {
  data() {
    var _this = this;
    var chkPwd = function(rule, value, callback) {
      // console.log(_this)//指向全部信息
      // console.log(this)
      if (value.trim() == "") {
        callback(new Error("请输入确认密码"));
      } else if (value == _this.form.password) {
        callback();
      } else {
        callback(new Error("密码不一致"));
      }
    };
    return {
      regShow: false,
      regShow1: true,
      covershow:true,
      form: {
        username: "",
        password: "",
        password2: "",
        email: "",
        phone: "",
        sex: "1"
      },
      rules: {
        //验证规则
        username: [
          {
            validator: function(rule, value, callback) {
              var usernameReg = /^[a-z]\w{2,17}$/;
              if (value.trim() == "") {
                callback(new Error("请输入用户名"));
              } else if (!usernameReg.test(value)) {
                callback(new Error("请输入3-18个字符，以小写字母开始"));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ],
        password: [
          {
            validator: function(rule, value, callback) {
              var len = value.length;
              if (value.trim() == "") {
                callback(new Error("请输入密码"));
              } else if (len < 6 || len > 18) {
                callback(new Error("密码为6-18个字符"));
              } else {
                //密码改变时，对password2进行触发
                //validateField 触发单个字段的验证规则
                _this.$refs.form.validateField("password2");
                callback();
              }
            }
            //默认验证方式就是change
          }
        ],
        password2: [
          {
            validator: chkPwd
          }
        ],
        email: [
          {
            validator: function(rule, value, callback) {
              var emailReg = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
              if (value.trim() == "") {
                callback(new Error("请输入邮箱"));
              } else if (!emailReg.test(value)) {
                callback(new Error("邮箱格式不合法"));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ],
        phone: [
          {
            validator: function(rule, value, callback) {
              var phonereg = /^[1][3,4,5,7,8][0-9]{9}$/;
              if (value.trim() == "") {
                callback(new Error("请输入手机号"));
              } else if (!phonereg.test(value)) {
                callback(new Error("手机格式不合法"));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    lxShow() {
      console.log(this);
      this.regShow = false;
      this.jQuery(".lg-leftv").css({ borderBottom: "2px solid #F23030" });
      this.jQuery(".lg-rightv").css({ borderBottom: "2px solid #fff" });
    },
    lxShow1() {
      this.regShow = true;
      this.jQuery(".lg-leftv").css({ borderBottom: "2px solid #fff" });
      this.jQuery(".lg-rightv").css({ borderBottom: "2px solid #F23030" });
    },
    covershows(){
        this.covershow=false
    }
  }
};
</script>
<style>
* {
  padding: 0px;
  margin: 0px;
  font-size: 14px;
  box-sizing: border-box;
  
}
p{
    margin-bottom:0;
}
#logq {
  background: #fff;
}
body {
  font-family: PingFang-SC-Regular;
}
.log-main {
  width: 90%;
  margin: 0 auto;
}

.lgname {
  border-bottom: 1px solid #cccccc;
  height: 48px;
  line-height: 48px;
  position: relative;
  margin-bottom: 5px;
  padding: 0 5px;
}
.lgname span {
  color: #333333;
}
.lgname input {
  border: none;
  outline: none;
  margin-left: 30px;
  width: 70%;
  height: 90%;
  font-family: PingFang-SC-Regular;
}
.lgname input::-webkit-input-placeholder {
  color: #ccc;
}
.icon-del {
  width: 22px;
  height: 22px;
  overflow: hidden;
  position: absolute;
  right: 10px;
  top: 12px;
  background-image: url(/static/img/iconBg.png);
  background-repeat: no-repeat;
  background-size: 100% auto;
  display: none;
}
.icon-del1 {
  width: 30px;
  height: 18px;
  overflow: hidden;
  position: absolute;
  right: 10px;
  top: 15px;
  background-image: url(/static/img/password.png);
  background-repeat: no-repeat;
  background-size: 100% auto;
}
.lg-btn {
  height: 45px;
  line-height: 46px;
  background-color: #eee;
  border-radius: 2px;
  text-align: center;
  color: #848689;
  margin-top: 30px;
}
.lg-btw {
  border: 1px solid #f23030;
  color: #f23030;
  background: #fff;
  margin-top: 10px;
  border-radius: 2px;
  text-align: center;
  height: 45px;
  line-height: 46px;
}
.lg-tip {
  margin-top: 20px;
}
.lg-tip span {
  font-size: 12px;
}
.lg-tip > span:last-child {
  float: right;
}
.lg-tip span span {
  margin-left: 26px;
}
.tip-left {
  width: 22px;
  height: 22px;
  display: inline-block;
  position: absolute;
  background: url(/static/img/iconBg.png) 0 -22px;
  background-repeat: no-repeat;
  background-size: 100% auto;
}
.tip-right {
  width: 22px;
  height: 22px;
  display: inline-block;
  position: absolute;
  background: url(/static/img/iconBg.png) 0 -42px;
  background-repeat: no-repeat;
  background-size: 100% auto;
}
.lg-space {
  margin-top: 105px;
  height: 200px;
  position: relative;
  padding: 24px 10px 0 10px;
  border-top: 1px solid #d7d7d7;
}
.lg-space h4 {
  position: absolute;
  top: -12px;
  left: 50%;
  background-color: white;
  padding: 0 10px;
  color: #bfbfbf;
  transform: translateX(-50%);
}
.other {
  margin: 20px auto;
  display: block;
  width: 22px;
  height: 22px;
  background: url(/static/img/iconBg.png) 0 -128px;
  background-repeat: no-repeat;
  background-size: 100% auto;
}
.lg-space p {
  text-align: center;
}

.reg-main {
  width: 90%;
  margin: 10px auto;
}
#reg-btn {
  width: 335px;
  margin-left: -70px;
}
.reg-tit {
  height: 45px;
  line-height: 45px;
  background: #f9f9f9;
  color: #616161;
  font-size: 17px;
  border-bottom: 1px solid #bbb;
  text-align: center;
  /* z-index: 999; */
}
.reg-icon {
  background: url(/static/img/iconBg.png) 0 -61px;
  width: 22px;
  height: 22px;
  display: inline-block;
  background-repeat: no-repeat;
  background-size: 100% auto;
  position: absolute;
  top: 10px;
  left: 12px;
}
#lgx-text {
  line-height: 44px;
  font-size: 14px;
  border-bottom: 1px solid #d7d7d7;
  display: flex;
  justify-content: space-around;
}
.lg-rightv {
  padding: 0 20px;
  height: 100%;
  line-height: 44px;
}
#lgx-text .lg-leftv {
  padding: 0 20px;
  height: 100%;
  line-height: 44px;
  border-bottom: 2px solid #f23030;
}
/* .lgx-text div:hover,.lgx-text div:active{
     border-bottom: 2px solid #F23030;
  }  */

  .agreement-dialog {
    top: 0;
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 1000;
    background: rgba(0,0,0,.5);
    font-family: PingFangSC-Regular;
    overflow: hidden;
}
.agreement-body {
    position: absolute;
    bottom: 0;
    padding: 0 10px;
    background: #f8f8f8;
    width: 100%;
    overflow: hidden;
    border-radius: 3px;
    box-sizing: border-box;
    height:367px;
}
.agreement-title {
    width: 100%;
    font-weight: bold;
    font-size: 18px;
    padding: 12px 0;
    text-align: center;
}
.agreement-line {
    border-top: 1px solid #d7d7d7;
    width: 104%;
    float: left;
    margin-left: -10px;
}
.agreement-content {
    width: 100%;
    height: 190px;
    font-size: 13px;
    padding-top: 12px;
    color: black;
    overflow-y: scroll;
    overflow-x: hidden;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    text-align: left;
}
.agreement-content p {
    font-size: 13px;
    line-height: 18px;
}
.agreement-btn-wrap {
    left: 0;
    bottom: 0;
    width: 100%;
    background: #f8f8f8;
    box-sizing: border-box;
}
.agreement-notice {
    text-align: left;
    font-size: 13px;
    padding: 10px 5px 0 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}
.agreement-notice span {
    font-weight: bold;
    line-height: 13px;
}
.agreement-btns {
    height: 55px;
    font-size: 16px;
    text-align: center;
}
.btn-cancel {
    float: left;
    color: #000;
    margin-left: 10px;
    background: #fff;
    border: 1px solid #d7d7d7;
}
.btn-complete {
    color: #fff;
    float: right;
    margin-right: 10px;
    border: 1px solid #E93B3D;
    background: #E93B3D;
}
.agreement-btn {
    display: inline-block;
    text-align: center;
    width: 40%;
    line-height: 39px;
    margin: 8px 0;
    border-radius: 3px;
}
.important-tip {
    font-weight: bold;
    text-decoration: underline;
}
.agreement-content a {
    color: #000;
}
.agreement-notice a {
    text-decoration: underline;
    color: #E93B3D;
}
</style>




